<script>
import Select from "./Select.svelte";
import { onMount } from "svelte";
import { select, selectAll } from "d3-selection";
import { descending, ascending, max, min } from "d3-array";
import { scaleLinear, scaleBand } from "d3-scale";
import { axisBottom, axisLeft } from "d3-axis";
import { transition } from "d3-transition";
import { diefstalrisico, diefstalrisicoTypes, diefstalrisicoHeaders } from "../data/diefstalrisico.js";

export let selectionValues;
// export let options;
// export let titleVar;

let el;

const datasets = [diefstalrisico, diefstalrisicoTypes];
const headers = diefstalrisicoHeaders;

// Get selection from Select component
let selected = selectionValues[0].value;

const data = [
  { name: 'Alice', math: 37, science: 62, language: 54 },
  { name: 'Billy', math: 43, science: 34, language: 85 },
  { name: 'Cindy', math: 86, science: 48, language: 71 },
  { name: 'David', math: 44, science: 21, language: 65 },
  { name: 'Emily', math: 59, science: 73, language: 29 }
];

onMount(async () => {
  const container = select(el);

  const margin = {
            top: 15,
            right: 25,
            bottom: 15,
            left: 60
        };
  const width = 960 - margin.left - margin.right,
        height = 500 - margin.top - margin.bottom;

  const highestNumber = max(data, item => item.math);

  // Create svg "canvas" to draw on
  const svg = container.append("svg")
    .attr("width", width + margin.left + margin.right)
    .attr("height", height + margin.top + margin.bottom)

  const chart = svg.append("g")
    .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

  const xScale = scaleLinear()
    .range([0, width])
    .domain([0, highestNumber]);

  const yScale = scaleBand()
    .domain(data.map(d => d.name))
    .range([height, 0], .1)

  //make y axis to show bar names
  const yAxis = axisLeft(yScale)
    //no tick marks
    .tickSize(0)

  const gy = chart.append("g")
    .attr("class", "y axis")
    .call(yAxis)

  const bars = chart.selectAll(".bar")
    .data(data)
    .enter()
    .append("g")

  //append rects
  bars.append("rect")
    .attr("class", "bar")
    .attr("y", function (d) {
      return yScale(d.name);
    })
    .attr("height", yScale.bandwidth())
    .attr("x", 0)
    .attr("width", function (d) {
      return xScale(d.math);
    });

  //add a value label to the right of each bar
  bars.append("text")
    .attr("class", "label")
    //y position of the label is halfway down the bar
    .attr("y", function (d) {
      return yScale(d.name) + yScale.bandwidth() / 2 + 4;
    })
    //x position is 3 pixels to the right of the bar
    .attr("x", function (d) {
      return xScale(d.math) + 3;
    })
    .text(function (d) {
      return d.value;
    });


  // chart
  //   .append('g')
  //   .attr('transform', `translate(0, ${height})`)
  //   .call(axisLeft(yScale))
  //   .selectAll('text')
  //   .style('text-anchor', 'end')
  //   .style('font-size', '18px')
  //   .attr('transform', 'rotate(-45) translate(-10, -5)');
