  //
  // function render (subject = 'math') {
  //   const update = svg.selectAll('rect')
  //     .data(data.filter(d => d[subject]), d => d.name);
  //
  //   update.exit()
  //     .remove();
  //
  //   update
  //     .transition()
  //     .attr('y', d => yScale(d[subject]))
  //     .attr('height', d => height - yScale(d[subject]));
  //
  //   update
  //     .enter()
  //     .append('rect')
  //     .style('fill', 'steelblue')
  //     .attr('x', d => xScale(d.name))
  //     .attr('width', xScale.bandwidth)
  //     .attr('y', d => yScale(d[subject]))
  //     .attr('height', d => height - yScale(d[subject]));
  // }
  //
  // render();
})
</script>

<style>
  .bar {
    fill: #5f89ad;
  }

  .axis {
    font-size: 13px;
  }

  .axis path,
  .axis line {
    fill: none;
    display: none;
  }

  .label {
    font-size: 13px;
  }
</style>

<section bind:this={el}>
  <button onclick="render('math')" class="bar axis label">Math</button>
  <button onclick="render('science')">Science</button>
  <button onclick="render('language')">Language</button>
